<template>
  <div class="write" ref="inputs">
    <div class="inputs">
      <textarea-autosize
        class="inputText title"
        placeholder="यहाँ शिर्षक लेख ..."
        rows="1"
        :max-height="100"
      />
      <textarea
        class="inputText text"
        ref="textInput"
        placeholder="यहाँ कुराहरु लेख अनि तलको पहेलो बटन थिच ..."
      ></textarea>
      <button v-if="spaceForButton">लेख्नुहोस</button>
    </div>
  </div>
</template>

<script>
import debounce from "debounce";
export default {
  created() {
    window.addEventListener("resize", this.debouncedResizeWatch);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.debouncedResizeWatch);
  },
  data() {
    return {
      spaceForButton: true,
      windowHeight: 0
    };
  },
  methods: {
    // textFocused() {
    //   if (window.innerHeight < 700) {
    //     this.spaceForButton = false;
    //   } else this.spaceForButton = true;
    // },
    // textBlurred() {
    //   this.spaceForButton = true;
    // },
    hello() {
      alert("fuck");
    },
    debouncedResizeWatch() {
      debounce(this.myEventHandler(), 300);
    },
    myEventHandler() {
      this.windowHeight = window.innerHeight;
      console.log("hello");
      this.$refs.inputs.style.height =
        parseInt(
          window.innerHeight - this.$refs.inputs.getBoundingClientRect().top
        ) + "px";
      this.$refs.textInput.style.height =
        parseInt(
          window.innerHeight -
            this.$refs.textInput.getBoundingClientRect().top -
            90
        ) + "px";
    }
  },
  mounted: function() {
    this.$nextTick(this.myEventHandler);
    this.windowHeight = window.innerHeight;
  },
  computed: {
    entered() {
      return this.$store.transStart;
    }
  },
  watch: {
    entered: () => {
      this.myEventHandler();
    },
    windowHeight(o, n) {
      n = Math.abs(n);
      if (n > 500) {
        this.spaceForButton = false;
        this.$refs.textInput.style.height =
          parseInt(
            window.innerHeight -
              this.$refs.textInput.getBoundingClientRect().top -
              10
          ) + "px";
      } else this.spaceForButton = true;
    }
  }
};
</script>

